.article_content {
  width: 70%;
  margin: auto;
  grid-template-columns: auto auto;
  grid-gap: 1px;
  padding: 0;
  iframe {
    width: 100%;
    height: 600px;
  }

  img {
    width: 100%;
  }
  h1,
  h2 {
    padding-top: $block2;
    padding-bottom: $block1;
  }
  p {
    margin-top: -5px;
    padding-bottom: $block1;
    text-align: justify;
  }
  a {
    color: $bodyColor;
  }
  a:hover {
    text-decoration: none;
  }
  ul {
    font-family: "basier_circleregular";
    font-size: $fontSize;
    line-height: 1.5rem;
  }

  blockquote {
    padding: $block3 $block3 $block2 $block3;
    p {
      font-family: "Cormorant Garamond", serif;
      font-size: 1.8rem;
      line-height: 1.8rem;

      font-style: italic;
    }
  }
}

.article_module {
  margin-top: $block5;
  margin-bottom: $block4;
}

.article_intro {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1px;
  padding: $block2 0 $block3 0;
}

.article_title {
  font-family: "Cormorant Garamond", serif;
  font-weight: 100;
  font-size: 6rem;
  line-height: 6rem;
  margin-bottom: -18px;
  margin-left: -5px;
  hyphens: auto;
  overflow: none;
}

.article_date {
  font-family: "basier_circlesemi";
  font-weight: 100;
  font-size: $fontSize;
  text-transform: uppercase;
  opacity: 0.3;
  text-align: right;
  width: 100%;
  align-self: flex-end;
  margin-bottom: -6px;
}

.article_particulars_container {
  display: grid;
  grid-template-columns: 40% 40% 20%;
  grid-gap: 1px;
  text-align: left;

}

.article_particulars {
  padding-right: $block3;
  p {
    text-align: left;
  }
}

@media only screen and (max-width: 728px) {
  .article_content {
    iframe {
      width: 100%;
      height: 250px;
    }
    h1,
    h2 {
      padding-top: $block1;
      padding-bottom: $block1;
    }
    p {
      margin-top: -10px;
      padding-bottom: $block1;
      text-align: justify;
    }
    a {
      color: $bodyColor;
    }
    a:hover {
      text-decoration: none;
    }

    blockquote {
      margin: 0;
      padding: $block1 $block1 $block1 $block1;
      p {
        font-family: "Cormorant Garamond", serif;
        font-size: 1.5rem;
        line-height: 1.5rem;

        font-style: italic;
      }
    }
  }

  .article_module {
    margin-top: $block5;
    margin-bottom: $block2;
  }

  .article_intro {
    display: grid;
    grid-template-columns: auto;

    padding: 0px 0 $block3 0;
  }

  .article_title {
    font-size: 4rem;
    line-height: 4rem;
  }

  .article_date {
    text-align: left;
    line-height: 1.8rem;
    padding: 24px 0 0 0;
  }
  .article_content {
    width: 90%;
  }

  .article_particulars_container {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1px;
    text-align: left;
    :nth-child(3) {
      text-align: left;
    }

  }

  .article_particulars {
    p {
      text-align: left;
    }
   
  }


}


@media only screen and (max-width: 1024px) and (min-width: 729px) {
  .article_particulars_container {
    grid-template-columns: 1fr 1fr;
  }

  .article_module {
    margin-top: $block5;
    margin-bottom: $block2;
  }

  .article_content {
    width: 90%;
    blockquote {
      margin: 0;
      padding: $block2 $block2 $block2 $block2;
      p {
        font-family: "Cormorant Garamond", serif;

        font-style: italic;
      }
    }
  }
}


@media screen and (min-width: 2400px) {
  .article_content {
    width: 50%;
  }
}