$backgroundColor: var(--background-color);
$bodyColor: var(--light-text-color);
$bodyFont: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji",
  "Segoe UI Emoji", "Segoe UI Symbol";
$fontSize: 0.9rem;

$block1: 8px;
$block2: 32px;
$block3: 64px;
$block4: 128px;
$block5: 192px;
$block: 24px;

h1 {
  font-family: "basier_circlesemi";
  font-weight: 100;
  font-size: $fontSize;
  text-transform: uppercase;
}

h2 {
  font-size: 0.8rem;
  text-transform: uppercase;
}

p {
  line-height: 1.5rem;
  font-size: $fontSize;
}

html,
html[data-theme="light"] {
  --background-color: #efedea;
  --light-text-color: #202020;
  --link-color: rgb(0, 112, 201);
  --main-background-color: rgb(255, 255, 255);
  --main-text-color: rgb(51, 51, 51);
}

html[data-theme="dark"] {
  --background-color: #202020;
  --light-text-color: #efedea;
  --link-color: rgb(100, 210, 255);
  --main-background-color: rgb(28, 28, 30);
  --main-text-color: rgb(229, 229, 234);
}

@media (prefers-color-scheme: dark) {
  html,
  html[data-theme="dark"] {
    --background-color: #202020;
    --light-text-color: #efedea;
    --link-color: rgb(100, 210, 255);
    --main-background-color: rgb(28, 28, 30);
    --main-text-color: rgb(229, 229, 234);
  }

  html[data-theme="light"] {
    --background-color: #efedea;
    --light-text-color: #202020;
    --link-color: rgb(0, 112, 201);
    --main-background-color: rgb(255, 255, 255);
    --main-text-color: rgb(51, 51, 51);
  }
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  margin: 0;
  padding: 0;
}
