.intro {
  font-family: "Cormorant Garamond", serif;
  font-size: 3rem;
  margin: 0;
  position: fixed;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 70%;
  z-index: -1;
  pointer-events: auto;
  text-align: justify;

  a {
    text-decoration: underline;
    color: $bodyColor;
  }
  a:hover {
    text-decoration: none;
    cursor: pointer;
  }
  sup {
    font-size: 1.2rem;
    text-align: center;
  }
}

.content {
  margin-top: 100vh;
  min-height: 100px;
  background-color: $backgroundColor;
  grid-gap: $block3;
}

.module {
  font-family: "basier_circlesemi";
  display: flex;
  width: 100%;
  p {
    padding: 2px 16px 2px $block3;
  }
}

.module_child {
  font-family: "basier_circlesemi";
  font-size: $fontSize;
  display: flex;
  width: 100%;
  border-style: solid;
  border-width: 1px 0 1px 0;

  p {
    padding: 2px 16px 2px $block3;
  }
}

.module_child_right {
  font-family: "basier_circleregular";
  font-size: $fontSize;
  display: flex;
  width: 100%;
  text-align: right;
  background-color: $bodyColor;
  color: white;
  a {
    text-decoration: underline;
    color: $backgroundColor;
  }
  a:hover {
    text-decoration: none;
  }
  p {
    width: 100%;
    padding: 2px $block3 2px $block3;
    text-align: right;
  }
}

.project_container {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 1px;
  padding: 0;
  background-color: $bodyColor;
}
.projects {
  padding: $block1;
  background-color: $backgroundColor;

  img {
    width: 100%; /* width of container */
    height: 300px; /* height of container */
    object-fit: cover;
  }
}

.project_info {
  z-index: 2;
  display: flex;
}

.project_title {
  z-index: 2;
  width: 100%;
  line-height: $block3;
  text-align: center;
  a {
    font-family: "basier_circlesemi";
    font-size: $fontSize;
    text-decoration: none;
  }
  border-style: solid;
  border-width: 1px;
}

.project_date {
  font-family: "basier_circleregular";
  font-size: $fontSize;
  width: 35%;
  border-style: solid;
  border-width: 1px;
  vertical-align: middle;
  text-align: center;
}

.about_column_parent {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: $block3;
  padding: 0;
  text-align: justify;
  z-index: 2;
  p {
    font-family: "basier_circleregular";
    font-size: $fontSize;
    line-height: 28px;
  }
  a {
    text-decoration: underline;
    color: $bodyColor;
  }
  a:hover {
    text-decoration: none;
    cursor: pointer;
  }
  sup {
    font-size: $fontSize;
    text-align: center;
  }
}

.project_image {
  position: relative;
  width: 100%;
}

.image {
  display: block;
  width: 100%;
  height: auto;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.2s ease;
  margin: auto;
  background-color: $backgroundColor;
}

.text {
  color: $bodyColor;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  opacity: 0;
}

.projects:hover .overlay {
  opacity: 0.95;
}

.projects:hover .overlay .text {
  margin: 0;
  padding: 0;

  h1 {
    font-size: 1.8rem;
    line-height: 2rem;
    font-family: "Cormorant Garamond", serif;
    text-transform: initial;
    margin: 0;
    padding: 0;
  }
  p {
    
    line-height: 1.5rem;
  }
  opacity: 1;
}

.about_module {
  height: 100vh;
  margin: auto;
  border-style: solid;
  border-width: 1px 0 1px 0;
  border-top: none;
}

.about_content {
  font-family: "Cormorant Garamond", serif;
  width: 70%;
  font-size: 3rem;
  margin: 0;
  position: relative;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.button {
  font-family: "Cormorant Garamond", serif;
  background-color: transparent; /* Green */
  border: solid 1px;
  color: $bodyColor;
  padding: 8px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition: 0.2s ease;
}

.button:hover {
  background-color: $bodyColor; /* Green */
  color: $backgroundColor;
}

.star {
  margin: auto;
  width: 8rem;
  fill: $bodyColor;
  display: none;
}

.eye {
  width: 8rem;
  fill: $bodyColor;
}

@media only screen and (max-width: 728px) {
  .intro {
    width: 90%;
    text-align: left;
    bottom: 80px;
    top: initial;
    font-size: 2rem;
    position: absolute;
    -ms-transform: translate(-50%, -0%);
    transform: translate(-50%, -0%);
  }
  .star {
    display: block;
    text-align: center;
  }

  .module {
    font-family: "basier_circlesemi";
    display: flex;
    width: 100%;
    p {
      padding: 2px 20px 2px 20px;
    }
  }

  .project_container {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 1px;
    padding: 0;
    background-color: $bodyColor;
  }
  .projects {
    padding: 20px;
    background-color: $backgroundColor;
    img {
      width: 100%; /* width of container */
      height: 200px; /* height of container */
      object-fit: cover;
    }
  }
  .about_column_parent {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 8px;
    padding: 0;
    text-align: justify;
  }

  .about_module {
    height: auto;

    border-style: solid;
    border-width: 1px 0 1px 0;
    border-top: none;
  }
  .about_content {
    width: 90%;
    padding: $block4 0 $block4 0;
    top: 0;
    left: 0;
    -ms-transform: none;
    transform: none;
    margin: auto;
  }
}

@media only screen and (max-device-width: 320px),
  only screen and (min-device-width: 560px) and (max-device-width: 1136px) and (-webkit-min-device-pixel-ratio: 2) {
  .star {
    width: 7rem;
    display: none;
    text-align: center;
  }
}

@media only screen and (max-width: 1024px) and (min-width: 729px) {
  .intro {
    width: 90%;
    text-align: left;
    bottom: 80px;
    top: initial;
    font-size: 2rem;
    position: absolute;
    text-align: center;
    -ms-transform: translate(-50%, -0%);
    transform: translate(-50%, -0%);
  }
  .star {
    width: 7rem;
    display: block;
    text-align: center;
  }

  .eye {
    margin: auto;
  }

  .about_module {
    height: auto;
    border-style: solid;
    border-width: 1px 0 1px 0;
    border-top: none;
  }

  .about_content {
    width: 90%;
    padding: $block5 0 $block5 0;
    top: 0;
    left: 0;
    -ms-transform: none;
    transform: none;
    margin: auto;
  }

  .about_column_parent {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 8px;
    padding: 0;
    text-align: justify;
  }

  .project_container {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 1px;
    padding: 0;
    background-color: $bodyColor;
  }
  .projects {
    padding: 20px;
    background-color: $backgroundColor;

    img {
      width: 100%; /* width of container */
      height: 250px; /* height of container */
      object-fit: cover;
    }
  }
}

@media screen and (min-width: 2400px) {
  .projects {
    padding: 20px;
    border-style: solid;
    border-width: 0 1px 1px 0px;
    justify-content: space-evenly;
    img {
      width: 100%; /* width of container */
      height: 500px; /* height of container */
      object-fit: cover;
    }
  }

  .intro {
    width: 50%;
  }
  .about_content {
    width: 50%;
  }
}

.first {
  width: 50px;
  cursor: pointer;

  position: absolute;
  bottom: 12px;

  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.3;
  fill: none;
  stroke: $bodyColor;
  stroke-width: 15;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

.first:hover {
  opacity: 1;
  transition: 0.2s ease;
  bottom: 8px;
}

@media (orientation: landscape) {
  .intro {
  }
  .star {
    display: none;
  }
}
